body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: -webkit-linear-gradient(right, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%);

  background: linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h3{
	font-size: 1.2rem;
	font-weight: 800;
}

p{
	font-size: 0.9rem;
	font-weight: 600;
}

img{
	padding: 0rem 2rem 0rem 2rem;
}

.car{
	max-height: 300px;
	max-width: 400px;
}
@font-face {
font-display: swap;	
font-family: 'SEGA LOGO FONT';
font-style: normal;
font-weight: normal;
src: local('SEGA LOGO FONT'), url(/robofriends/static/media/SEGA.12a3db8b.woff) format('woff');
}

h1{
	font-family: 'SEGA LOGO FONT';
	font-weight: 200;
	color : #0ccac4;
}

.car{
	max-width: 400px;
	height: 300px;
	border-radius: 20px;
}

.logo{
	width: 240px;
	height : 80px;
}
